@import './reset.scss';

$메인칼라 : #ff0000;

.red {
  color : $메인칼라;
}

// 특정 요소 포함된 함수
@mixin 함수() {
  background: #ffe591;
}

.my-alert {
  background: #eeeeee;
  padding : 20px;
  border-radius: 5px;
  max-width:  500px;
  width : 100%;
  margin: auto;
  // 요소 안 요소들 따로 설정
  p {
    margin-bottom: 0px;
  }
}

.my-alert2 {
  @extend .my-alert; //복붙-1
  @include 함수(); //복붙-2
}

